.team__wrapper-outer {
  border: 1px solid $brown;
  padding: 22px 31px 22px 25px;
  width: 64.2%;
  margin-left: auto;

  img {
    width: 360px;
    height: 356px;
  }
}

@media (max-width: $tablet-max-width) {
  .team__wrapper-outer {
    padding: 23px 23px 23px 25px;
    width: 100%;
    margin-left: 0;

    img {
      width: 207px;
      height: 208px;
    }
  }
}

@media (max-width: $mobile-max-width) {
  .team__wrapper-outer {
    border: none;
    padding: 0;

    img {
      width: 166px;
      height: 166px;
    }
  }
}
